<template>
  <div class="otc-page">
    <div class="otc-title">
      <div class="left">{{ $t('otc.title') }}({{ $t('otc.agent') }})</div>
      <div class="right" @click="$gbUtils.handleToPage('/otcAgentHangOrder')">
        <span>{{ $t('otc.myOrder') }}</span>
        <van-icon name="arrow" />
      </div>
    </div>
    <div class="last-price">
      <div class="price-box">
        <span>{{ lastPrice }}</span>
        <span>MUSD/MAI</span>
      </div>
      <div class="ptice-tips">{{ $t('otc.lastPrice') }}</div>
    </div>
    <!-- 卖单 -->
    <OrderBox :type="2" :lastPrice="lastPrice || 0"/>
    <!-- 买单 -->
    <OrderBox :type="1" :lastPrice="lastPrice || 0" />
  </div>
</template>

<script>
import contractOtc from '../../contracts/contractOtcAgent'
import OrderBox from './components/OrderBox.vue'

export default {
  name: '',
  components: { OrderBox },
  data () {
    return {
      lastPrice: 0,
      type: 2 // 1买; 2-卖
    }
  },
  methods: {
    async getLastPrice () {
      const resp = await contractOtc.lastPrice()
      if (!resp.success) return
      this.lastPrice = resp.result
    },
    init () {
      this.getLastPrice()
    }
  },
  mounted () {
    this.init()
  }
}
</script>

